@keyframes dropCard{
    from{
        transform: translate(0, -100vh);
    }
    to {
        transform: translate(0, 0);
    }
}

.animation {
    animation-name: dropCard;
    animation-duration: 2s;
    transform: translate(0, -100vh);    
    animation-fill-mode: forwards;
}
.card-container{
    transform: translate(0, -100vh);    
}